import GridItem from "./GridItem";
const GridRow = (props) => {
	return (
		<div className="gridrow">
			{props.row.map((col, colIndex) => (
				<GridItem
					value={col}
					rowIndex={props.rowIndex}
					colIndex={colIndex}
					playerClick={props.playerClick}
					playerMove={props.playerMove}
				/>
			))}
		</div>
	);
};
export default GridRow;
