import React, { useEffect, useState } from "react";
import "./App.css";
import GridRow from "./GridRow";

const App = () => {
	//STATES
	const [alreadyWon, setAlreadyWon] = useState(false);
	const [boardState, setBoardState] = useState([
		["", "", ""],
		["", "", ""],
		["", "", ""],
	]);
	const [playerMove, setplayerMove] = useState("X");

	useEffect(() => {}, [boardState, playerMove]);

	const playerClick = (i, j) => {
		if (boardState[i][j] === "") {
			const currBoardState = boardState;
			currBoardState[i][j] = playerMove;
			setBoardState(currBoardState);
			setplayerMove(playerMove === "X" ? "O" : "X");
			setTimeout(() => winningConditionsHandler(), 0);
		}
	};

	const winningConditionsHandler = () => {
		let win = true;
		let draw = false;
		//ROWS CHECK
		for (let i = 0; i < 3; i++) {
			win = true;
			for (let j = 1; j < 3; j++) {
				if (boardState[i][j] !== boardState[i][j - 1]) {
					win = false;
					break;
				}
			}
			if (win && boardState[i][0] !== "") {
				// console.log("won");
				winORdrawAlert(boardState[i][0]);
			}
		}
		//COLUMNS CHECK
		for (let i = 0; i < 3; i++) {
			win = true;
			for (let j = 1; j < 3; j++) {
				if (boardState[j][i] !== boardState[j - 1][i]) {
					win = false;
					break;
				}
			}
			if (win && boardState[0][i] !== "") {
				winORdrawAlert(boardState[0][i]);
			}
		}
		//FORWARD DIAGONAL CHECK
		win = true;
		for (let i = 1; i < 3; i++) {
			if (boardState[i][i] !== boardState[i - 1][i - 1]) {
				win = false;
				break;
			}
		}
		if (win && boardState[0][0] !== "") {
			winORdrawAlert(boardState[0][0]);
		}
		//ANTI-DIAGONAL CHECK
		win = true;
		for (let i = 1; i < 3; i++) {
			if (boardState[i][2 - i] !== boardState[i - 1][2 - i + 1]) {
				win = false;
				break;
			}
		}
		if (win && boardState[0][2] !== "") {
			winORdrawAlert(boardState[0][2]);
		}
		//DRAW CHECK
		draw = true;
		for (let i = 0; i < 3; i++) {
			for (let j = 0; j < 3; j++) {
				if (boardState[i][j] === "") {
					draw = false;
					break;
				}
			}
			if (draw === false) {
				break;
			}
		}
		if (draw) {
			winORdrawAlert("D");
		}
	};

	const winORdrawAlert = (thisWon) => {
		if (thisWon === "X") {
			alert("Congratulations! Player1 wins");
			setAlreadyWon(true);
		} else if (thisWon === "O") {
			alert("Congratulations! Player2 wins");
			setAlreadyWon(true);
		} else {
			if (alreadyWon === false) alert("Draw!");
		}
		//INITIALIZING THE GAME BACK
		setBoardState([
			["", "", ""],
			["", "", ""],
			["", "", ""],
		]);
		setplayerMove("X");
		// setAlreadyWon(false);
	};

	return (
		<div>
			<div className="container">
				<div className="header">TIC TAC TOE</div>
				<div className="board">
					{boardState.map((row, rowIndex) => (
						<GridRow
							row={row}
							rowIndex={rowIndex}
							playerClick={playerClick}
							playerMove={playerMove}
						/>
					))}
				</div>
				<div className="turn" id="turn">
					{`Player ${playerMove} turn`}
				</div>
			</div>
		</div>
	);
};

export default App;
